import { React } from 'jimu-core';
import classNames = require('classnames');
import {
  Card as BSCard, CardImg, CardText, CardBody, CardTitle, CardSubtitle
} from 'reactstrap';
import { Image, ImageProps } from '../image';
import { CollapsibleText } from '../text-collapsible';
import { Sanitizer } from '@esri/arcgis-html-sanitizer';

const prefix = 'jimu';

export type ContentJustifyType = 'start' | 'center' | 'end';

export interface CardProps {
  title: string,
  image?: ImageProps,
  description?: string,
  textLimit?: number, // number of characters to display for "description"
  isRichText?: boolean, // if it is from a rich text field
  className?: string,
  horizontal?: boolean,
  contentJustify?: ContentJustifyType
}

export interface CardState { }

export class Card extends React.PureComponent<CardProps, CardState> {

  render() {
    const {
      title,
      description,
      image,
      textLimit,
      isRichText,
      className,
      horizontal,
      contentJustify
    } = this.props;

    const img = image && <Image {...image} className='card-img' />;
    const header = title && <CardTitle className={description ? '' : 'mb-0'}>{title}</CardTitle>;

    let content;
    if (description) {
      if (typeof description === 'string') {
        content = <CardText> {
          textLimit !== null ?
            <CollapsibleText text={description} limit={textLimit} />
            : isRichText ?
              <span dangerouslySetInnerHTML={{ __html: (new Sanitizer()).sanitize(description) }}></span>
              : description
        }</CardText>;
      } else {
        content = description;
      }
    }

    const classes = classNames(
      className,
      prefix + '-card',
      { 'card-horizontal': horizontal }
    );

    const bodyClasses = classNames({
      'd-flex flex-column': horizontal,
      'justify-content-start': contentJustify === 'start',
      'justify-content-center': contentJustify === 'center',
      'justify-content-end': contentJustify === 'end'
    });

    return (
      <BSCard className={classes}>
        {img}
        <CardBody className={bodyClasses}>
          {header}
          {content}
        </CardBody>
      </BSCard>)
  }
}
