import { React, ReactDOM } from 'jimu-core';
import { Button } from 'reactstrap';
import classNames = require('classnames');

const prefix = 'jimu';

export interface CollapsibleTextProps {
  text: string,
  limit?: number
}

export interface CollapsibleTextState {
  collapsed: boolean
}

export class CollapsibleText extends React.PureComponent<CollapsibleTextProps, CollapsibleTextState> {

  toggle() {
    this.setState(prevState => ({
      collapsed: !prevState.collapsed
    }));
  }

  constructor(props) {
    super(props);

    this.state = {
      collapsed: true
    };

    this.toggle = this.toggle.bind(this);
  }

  _processText(text: string, limit: number) {
    // TODO: add rich text support
    const c = text.substr(0, limit);
    const h = text.substr(limit, text.length - limit + 1);
    return <span>{c}<span className='ellipses'>...</span>
      <span className='more-content'>
        <span className='more-content--text'>
          {h}
        </span>
        {
          /* TODO: add this back when flex layout is ready:
          <Button color='link' size='xs' onClick={this.toggle}>
          See {this.state.collapsed ? 'More' : 'Less'}
        </Button> */
        }
      </span>
    </span>;
  }

  render() {
    const {
      text,
      limit
    } = this.props;

    const classes = classNames(
      prefix + '-text-collapsible',
      {'collapsed' : this.state.collapsed }
    );

    if (isNaN(limit) || text.length <= limit) {
      return text;
    } else {
      return <span className={classes}>{
        this._processText(text, limit)
      }</span>
    }
  }

}

