import * as React from 'react';
import classNames = require('classnames');
import {withHistory, History, Location, urlUtils, createLocation} from 'jimu-core';

//learn from: https://github.com/ReactTraining/react-router/blob/master/packages/react-router-dom/modules/Link.js

const prefix = 'jimu';

export interface LinkProps {
  onClick?: (evt: React.MouseEvent<HTMLLinkElement>) => void;
  target?: string;
  replace?: boolean;
  history?: History;
  to: string | {
    pathname: string;
    search: string;
    hash: string;
  };
  innerRef?: string | ((node) => void);
  className?: string;
  style?: React.CSSProperties;
}

const isModifiedEvent = event =>
  !!(event.metaKey || event.altKey || event.ctrlKey || event.shiftKey);

export class _Link extends React.PureComponent<LinkProps>{
  href: string;

  handleClick = event => {
    if (this.props.onClick) this.props.onClick(event);

    if (
      !event.defaultPrevented && // onClick prevented default
      event.button === 0 && // ignore everything but left clicks
      !this.props.target && // let browser handle "target=_blank" etc.
      !isModifiedEvent(event) // ignore clicks with modifier keys
    ) {
      event.preventDefault();

      const { replace, history } = this.props;

      if (replace) {
        history.replace(this.href);
      } else {
        history.push(this.href);
      }
    }
  };

  render(){
    const { replace, to, innerRef, history, style, className, ...props } = this.props;

    const classes = classNames(
      prefix + '-link',
      className
    );

    if(!to){
      console.error('You must specify the "to" property');
      return null;
    }

    const location =
      typeof to === "string"
        ? createLocation(history.location, to)
        : to;

    const href = history.createHref(location);
    this.href = href;
    return (
      <a {...props} onClick={this.handleClick} href={href} ref={innerRef} className={classes} style={style}/>
    );
  }
}

export const Link = withHistory(_Link);