import { React, ReactDOM } from 'jimu-core';
import { ListGroupItem } from 'reactstrap';
import { TextWithIconProps } from './types';
import { Card, CardProps } from '../card/card';
import { Link } from '../link';
import { Icon } from '../../style/assets/icons/icon';
import classNames = require('classnames');

const prefix = 'jimu';

// const enum AcceptableType {
//   STRING,
//   CARD
// }

export type ListItemType = string | TextWithIconProps | CardProps;

interface linkProps {
  to: string
}

export interface ListItemProps extends linkProps {
  key?: number,
  className?: string,
  width?: number,
  height?: number,
  data: ListItemType,
  selectable?: boolean,
  active?: boolean,
  showActive?:boolean,
  onClick?: (e) => void
  onChange?: (listItem) => void
}

export interface ListItemState {
  isActive?: boolean
}


function isStringype(arg: any): arg is string {
  return typeof arg === 'string';
}

function isIconType(arg: any): arg is CardProps {
  return arg && arg.icon !== undefined;
}

function isCardType(arg: any): arg is CardProps {
  return arg && arg.title !== undefined;
}

class LinkWrapper extends React.Component<linkProps> {
  constructor(props) {
    super(props);
  }

  render() {
    if (this.props.to) {
      return <Link to={this.props.to}>{this.props.children}</Link>;
    }
    return this.props.children;
  }
}

// TODO: might need to move this as its own component
class TextWithIcon extends React.PureComponent<TextWithIconProps> {
  constructor(props) {
    super(props);
  }
  render() {
    const {
      icon,
      text
    } = this.props;
    return <span><Icon className='mr-4' icon={icon.name}/>{text}</span>
  }
}

export class ListItem extends React.PureComponent<ListItemProps, ListItemState> {

  private _handleChange = listItem => {
    return this.props.onChange(listItem);
  }

  private _handleClick = e => {
    if (this.state.isActive) return;

    this.setState(prevState => ({
      isActive: !prevState.isActive
    }));

    this._handleChange(this);
  }

  public unselect() {
    this.setState({ isActive: false });
  }

  constructor(props) {
    super(props);

    this.state = {
      isActive: this.props.active
    }

    if (this.props.active) {
      this._handleChange(this);
    }
  }

  componentDidUpdate(prevProps: AllWidgetProps<IMConfig>) {
    if(this.props.active !== prevProps.active) {
      this.setState(prevState => ({
        isActive: !prevState.isActive
      }));
    }
  }

  render() {
    const {
      data,
      width,
      height,
      className,
      selectable,
      onClick,
      to,
      showActive,
      ...otherProps
    } = this.props;

    const classes = classNames(
      className,
      prefix + '-list--item',
      {
        'p-0': isCardType(data),
        'selectable': selectable
      }
    );

    const styles = {
      width: width ? `${width}px` : null,
      height: height ? `${height}px` : null
    }

    return <ListGroupItem
      className={classes}
      style={styles}
      active={showActive&&this.state.isActive}
      onClick={selectable ? this._handleClick : null}>
      <LinkWrapper to={to}>
        {isStringype(data) && (data)}
        {isIconType(data) && (<TextWithIcon {...data as TextWithIconProps} /> )}
        {isCardType(data) && (<Card {...data as CardProps} />)}
      </LinkWrapper>
    </ListGroupItem>
  }
}