import { React, ReactDOM } from 'jimu-core';
import { ListGroup } from 'reactstrap';
import { ListItem, ListItemType } from './list-item';
import classNames = require('classnames');
import * as Immutable from 'seamless-immutable';

const prefix = 'jimu';

export interface ListProps {
  dataSource: ListItemType[],
  horizontal?: boolean,
  cardWidth?: number | string
  className?: string,
  selectable?: boolean,
  selectedIndexes?: number[],
  canWrap?: boolean,
  to?: string,
  highlightSelection?: boolean,
  onChange?: (listItem) => void
}

export interface ListState {
  selectedItem: ListItem
}

export class List extends React.PureComponent<ListProps, ListState> {
  readonly defaultHorizontalCardWidth = 320;

  private _handleItemChange = (listItem: ListItem): void => {
    let selectedItem = this.state.selectedItem;
    if (selectedItem) {
      selectedItem.unselect();
    }
    this.setState({
      selectedItem: listItem
    });
    this.props.onChange(listItem);
  }

  constructor(props) {
    super(props);

    this.state = {
      selectedItem: null
    };
  }
  
  render() {
    const {
      className,
      horizontal,
      cardWidth,
      selectable,
      selectedIndexes,
      canWrap,
      to,
      highlightSelection,
      ...otherProps
    } = this.props;

    const dataSource: Array<ListItemType> = this.props.dataSource;

    const classes = classNames(
      className,
      prefix + '-list',
      {
        'list-horizontal flex-row': horizontal,
        'flex-nowrap': !canWrap,
        'flex-wrap': canWrap,
        'selectable': selectable
      }
    );

    return (
      <ListGroup className={classes}>
        {dataSource.map((l: ListItemType, i) => {
          return <ListItem
            key={i}
            data={l}
            active={highlightSelection&&selectedIndexes && selectedIndexes.indexOf(i) > -1}
            width={cardWidth as number || (horizontal ? this.defaultHorizontalCardWidth : null)}
            selectable={selectable}
            to={to}
            showActive={highlightSelection}
            onChange={this._handleItemChange} />
        })}
      </ListGroup>
    );
  }
}