import { React } from 'jimu-core'
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { IMUser } from 'jimu-core';

// TODO: extend or mixin dropdown props?
interface UserMenuProps {
  user?: IMUser;
  onSignIn: () => void;
  onSignOut: () => void;
}

export class UserMenu extends React.PureComponent<UserMenuProps, {dropdownOpen: boolean}>{

  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      dropdownOpen: false
    };
  }

  toggle() {
    this.setState(prevState => ({
      dropdownOpen: !prevState.dropdownOpen
    }));
  }

  render(){
    const {
      user
    } = this.props;

    // TODO: show thumbnail image too
    const content = user ?
      <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
        <DropdownToggle color="primary" caret>
          {user.fullName ? user.fullName : user.username}
        </DropdownToggle>
        <DropdownMenu>
          <DropdownItem onClick={this.props.onSignOut}>Sign out</DropdownItem>
        </DropdownMenu>
      </Dropdown> :
      <button className="btn btn-outline-primary" onClick={this.props.onSignIn}>Sign in</button>;

    return <div className="jimu-user-menu">{content}</div>;
  }
}
