import { React } from 'jimu-core';
import classNames = require('classnames');

const prefix = 'jimu';

export interface SwitchProps {
  className?: string;
  checked?: boolean;
  disabled?: boolean;
  onClick?: (e: React.MouseEventHandler<HTMLSpanElement>) => void;
  onChange?: (checked: boolean) => any;
}

export interface SwitchState {
  checked: boolean;
}

export class Switch extends React.PureComponent<SwitchProps, SwitchState> {

  private _handleClick = (e: React.MouseEvent<HTMLSpanElement>) => {
    const onClick = this.props.onClick;
    if (!this.props.disabled) {
      if (onClick) {
        onClick(e);
      }
      this.setChecked(!this.state.checked);
    }
  }

  static getDerivedStateFromProps(props, state){
    return {checked: props.checked};
  }

  setChecked(checked) {
    const onChange = this.props.onChange;
    this.setState({
      checked: checked
    });
    if(onChange) {
      onChange(checked);
    }
  }

  constructor(props) {
    super(props);

    this.state = {
      checked: this.props.checked
    }

    this._handleClick.bind(this);
  }

  render() {
    const {
      className,
      disabled
    } = this.props;

    const classes = classNames(
      prefix + '-switch',
      className,
      {
        'checked': this.state.checked,
        'disabled': disabled
      }
    );

    return (
      <span
        className={classes}
        onClick={this._handleClick}
      >
        <span className='switch-slider'></span>
      </span>
    );
  }
}
