import { React, ReactDOM } from 'jimu-core';
import * as PropTypes from 'prop-types';
import classNames = require('classnames');

const prefix = 'jimu';

export type ImageType = 'fluid' | 'thumbnail';
export type ImageShapeType = 'circle' | 'rectangle';

export interface ImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  type?: ImageType,
  shape?: ImageShapeType,
  cover?: boolean
}

export interface ImageStates { }

export class Image extends React.PureComponent<ImageProps, ImageStates> {
  readonly defaultCircledImageSize = 100;
  readonly defaultCoveredImageHeight = 160;

  render() {
    const {
      type,
      shape,
      width,
      height,
      cover,
      className,
      ...otherImageProps
    } = this.props;

    const classes = classNames(
      prefix + '-img',
      className,
      type ? `img-${type}` : '',
      {
        'img-circle': shape === 'circle',
        'img-covered': cover
      }
    );

    if (cover || shape === 'circle') { // need a wrapper
      let styles;

      if (cover) {
        styles = {
          height: `${height || this.defaultCoveredImageHeight}px`,
          backgroundImage: `url(${this.props.src})`
        }
      }

      if (shape === 'circle') {
        const circleSize = (width && height) ? (width < height ? width : height) : (width || height || this.defaultCircledImageSize);
        styles = {
          width: `${circleSize}px`,
          height: `${circleSize}px`
        }
      }

      return <div className={classes} >
        <div className='img-wrapper' style={styles}>
          {cover ? null : <img {...otherImageProps} />}
        </div>
      </div>
    } else {
      return <img {...otherImageProps} className={classes} />
    }
  }

}

