import * as React from 'react';
import * as PropTypes from 'prop-types';

import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames = require('classnames');

interface TabProps{
  active?: boolean;
  key?: string;
  title: string;
  children: JSX.Element;
}
export class Tab extends React.PureComponent<TabProps>{
  render(){
    return null;
  }
}

interface TabsProps{
  onTabSelect?: (title: string) => void
}
interface TabsState{
  activeTab: string,
}
export class Tabs extends React.PureComponent<TabsProps, TabsState>{
  constructor(props) {
    super(props);

    this.selectTab = this.selectTab.bind(this);

    let activeChild;
    React.Children.forEach(this.props.children, c => {
      if(!React.isValidElement(c)){
        return;
      }
      let tabProps = c.props as TabProps;
      if(tabProps.active){
        activeChild = tabProps.title;
      }
    });
    this.state = {
      activeTab: activeChild
    };
  }

  selectTab(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
      this.props.onTabSelect && this.props.onTabSelect(tab);
    }
  }

  render(){
    return <div>
      <Nav tabs>
        {React.Children.map(this.props.children, (child) => {
          if(!React.isValidElement(child)){
            return;
          }
          let tabProps = child.props as TabProps;
          return <NavItem>
            <NavLink className={classnames({ active: this.state.activeTab === tabProps.title })}
              onClick={() => { this.selectTab(tabProps.title); }}>{tabProps.title}</NavLink>
          </NavItem>
        })}
      </Nav>
      <TabContent activeTab={this.state.activeTab}>
        {React.Children.map(this.props.children, (child) => {
          if(!React.isValidElement(child)){
            return;
          }
          let tabProps = child.props as TabProps;
          return <TabPane tabId={tabProps.title}>{tabProps.children}</TabPane>
        })}
      </TabContent>
    </div>;
  }
}

